@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}
body {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 16px;
  font-weight: normal;
}

#statusBarContainer {
  width: 100vw;
  height: 5px;
  position: fixed;
}

#statusBar {
  border-radius: 0 5px 5px 0;
  background-color: #e74c3c;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  height: 5px;
  width: 100%;
}

#postHeaderWrapper {
  width: 100%;
  min-width: 800px;
  height: 100vh;
  min-height: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-align: center;
}

#postHeaderTitle {
  font-size: 3.2rem;
  font-weight: bold;
  text-align: center;
  margin: 20px 0;
}

#postHeaderHr {
  width: 50px;
  height: 2px;
  border: none;
  background-color: white;
}

@-webkit-keyframes textHover {
  from {
    color: white;
  }
  to {
    color: #f37263;
  }
}

@keyframes textHover {
  from {
    color: white;
  }
  to {
    color: #f37263;
  }
}

#postHeaderDetails {
  margin: 20px 0;
  font-style: italic;
}

#postHeaderCategory {
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
}

#postHeaderDay {
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  margin-left: 10px;
}

.headerTag + .headerTag {
  margin-left: 5px;
}

#postHeaderCategory:hover {
  -webkit-animation: textHover 1s;
          animation: textHover 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

#postBodyWrapper {
  width: 1200px;
  height: 5000px;
  padding: 100px 0 0;
  margin: 0 auto;
}

